@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400;700&display=swap);
@-webkit-keyframes slideUp {
  0% {
    -webkit-transform: translateY(100%) scale(0);
            transform: translateY(100%) scale(0);
    opacity: .5;
  }
  100% {
    -webkit-transform: translateY(0%) scale(1);
            transform: translateY(0%) scale(1);
    opacity: 1;
  }
}

@keyframes slideUp {
  0% {
    -webkit-transform: translateY(100%) scale(0);
            transform: translateY(100%) scale(0);
    opacity: .5;
  }
  100% {
    -webkit-transform: translateY(0%) scale(1);
            transform: translateY(0%) scale(1);
    opacity: 1;
  }
}

html {
  font-family: 'Roboto Condensed', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(rgba(0,0,0,.5), rgba(0,0,0,.5)), url(/Pr0ph3t-287/sduplessisportfolio.github.io/static/media/pexels-jeremy-bishop-2524874.6f7f54fc.jpg);
  background-size: cover;
  min-height: 100vh;
  margin: 0;
}

body {
  background-color: transparent !important;
  margin: 0;
  overflow: hidden;
}

.App {
  display: flex;
  flex-direction: column;
  

}

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 90vh;
  -webkit-animation: .5s ease-out 0s 1 slideUp;
          animation: .5s ease-out 0s 1 slideUp;
  
}

.space {
  margin: 20px 0px;
}

.element {
  margin: 20px 0;
}

.links {
  margin: 0px 5px;
}

.svg-inline--fa {
  font-size: 2em;
  color: rgba(232,230,227,.87) !important;
}

/*-------------- TITLE --------------*/

.title h1 {
  font-size: 3.3em;
}

/*-------------- ABOUT --------------*/

.about p{
  width: 350px;
  text-indent: 1.5em;
  font-size: 1.1em;
}

/*-------------- NAVIGATION --------------*/

.navigation ul {
  display: flex;
  align-items: center;
  list-style-type: none;
  padding-left: 0;
  margin: 0;
}

.navigation ul li button {
  border: none;
  opacity: 1;
  background-color: transparent;
  padding: 0px 15px;
  margin: 0px 10px;
  font-size: 1.3em;
}

.navigation ul li button:hover {
  cursor: pointer;
}

/*-------------- FOOTER --------------*/

footer {
  display: flex !important;
  justify-content: center;
  font-size: .8em;
  opacity: .7;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;   
}










/* Change This */

Form {
  background-color: rgba(0,0,0,.5);
  border: 1px solid lightgray;
  border-radius: 10px;
  padding: 20px;
}
